/* navbar css */
.navbar {
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0.3px solid gray !important;

}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 1px solid #7A8AAB;
}

.navbar-brand {
  font-weight: 700;
  font-family: 'roboto', sans-serif;
  letter-spacing: 3px;
}

/* -------------------------Footer--------------------------- */
.footer {
  color: white;
  padding: 25px;


  background: linear-gradient(to right, #7A8AAB, rgb(3, 29, 91));
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: antiquewhite;
  border-bottom: 1px solid #ffefba;

}


/* /---------------------page not found css */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;

}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;

}

/* contact page */
.contact-us {
  height: 100vh;
}

.contact-img {
  margin-top: 40px;
}

.contact-data {
  margin-top: 100px !important;
  margin: 50px;
}

.contact-info {
  margin: 50px;
}

/* ---register page---- */
.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;

}

/* product */
.product-link {
  text-decoration: none !important;
  color: black !important;



}

/* admin */
.product-admin {
  display: flex;
  flex-wrap: wrap;

}

/* about-section */
/* .about {
  display: flex;
  height: 100vh;

} */

.about-img {
  margin-top: 100px;
  margin-left: 40px;
  height: 400px;

}

.about-data {
  margin: 100px;
}

/* policy-page */
.policy-img img {
  margin-top: 100px;
}

.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* admin dashboard */
.admin-d {
  margin-top: 100px;

}

.admin-info {
  background: linear-gradient(rgb(59, 82, 105), rgb(0, 78, 0));
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
}

/* category-btn */
.category-btn {
  background-color: rgb(37, 93, 157);
  padding: 50px;
  color: white;
  font-weight: 700;
  font-size: 20px;
  font-family: 'Courier New', Courier, monospace;
}