@import url('https://fonts.googleapis.com/css2?family=Kdam+Thmor+Pro&family=Poppins:wght@100;200;300;400&family=Roboto+Mono&display=swap');

.banner-img {
    /* margin-top: 60px; */
    height: 350px;
    position: relative;

}


.home-page h1 {
    color: gray;
    font-family: "Playfair Display", serif;
}

.home-page {
    display: flex;
    flex-wrap: wrap;
}

.home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
    justify-content: center;
    /* background-color: pink; */
}

.home-page .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.home-page .card-price {
    color: rgb(111, 160, 166);
    font-weight: bold;
}

.home-page button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
}

.home-page .card-text {
    color: rgb(90, 89, 89);
}

.home-page .card img:hover {
    transform: scale(0.9);
}

.home-page .card-img-top {
    height: 250px !important;
}


/* ==================
  filters
  ============== */
.filters h4 {
    margin-top: 100px;
    color: #6B789D !important;
    border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.reset-btn {
    color: #2A3113;
    padding: 10px;
    background-color: #717EA6 !important;
}

.reset-btn:hover {
    background-color: #33394E !important;
    color: #717EA6;
}

.hello {}

.hello-1 {}

.hello-3 {

    width: 400px !important;

}

.ant-checkbox-wrapper:first-of-type {
    margin-left: 8px;
}

.ant-checkbox-wrapper {
    margin: 2px;
}

.ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
}

.filters button {
    background-color: black;
    width: 100%;
    border: none;
    border-radius: 0;
    margin-top: 20px;
}

.loadmore {
    color: green;
    font-weight: bold;
    font-size: 20px !important;
}

/* static content */
.static {
    background-color: #A0A0B4;
    /* height: 500px; */
    display: flex;
    flex-direction: column;
    gap: 100px;
    padding: 40px;
}

.top {
    display: flex;
    gap: 50px;
    padding: 50px;

}

.left {
    /* background-color: red; */
    font-size: 50px;
    flex-basis: 60%;
    font-family: "Kdam Thmor Pro", sans-serif;


}


.right {
    /* background-color: blue; */
    font-size: 20px;
    color: white;
    flex-basis: 40%;
    font-family: "Roboto Mono", sans-serif;
}

.item-cards {
    display: flex;
    gap: 40px;
}

.card-1 {
    height: 400px;
    width: 350px;
    transition: all .2s ease-in-out;
}

.card-1:hover {
    transform: rotate(10deg);
}